import axios from 'axios';

const API_KEY = '5cb4dd4cf3f1476227d92f7c4b196044';
const BASE_URL = 'https://api.themoviedb.org/3';

export class GetMovie {
  page = 1;
  query = null;
  movie_id = null;
  //         
  async getTrandMovies(page) {
    try {
      const params = {
        api_key: API_KEY,
        language: 'en-US',
        page: page,
      };
      const response = await axios.get(`${BASE_URL}/trending/movie/week?`, {
        params,
      });
      return response.data;
    } catch (error) {
      console.log(error);
    }
  }

  //       
  async getMoviesByName(page) {
    try {
      const params = {
        api_key: API_KEY,
        language: 'en-US',
        page: page,
        query: this.query,
      };
      const response = await axios.get(`${BASE_URL}/search/movie?`, {
        params,
      });
      return response.data;
    } catch (error) {
      console.log(error);
    }
  }

  //      
  async getMovieFullInfo(id) {
    // try {
    //   const params = {
    //     api_key: API_KEY,
    //     language: 'en-US',
    //   };
    //   const response = await axios.get(`${BASE_URL}/movie/${id}?`, {
    //     params,
    //   });
    //   return response.results;
    // } catch (error) {
    //   console.log(error);
    // }
    return fetch(`https://api.themoviedb.org/3/movie/${id}?api_key=5cb4dd4cf3f1476227d92f7c4b196044&language=en-US`).then(response => response.json())
  }

  //   
  async getMovieTrailer() {
    try {
      const params = {
        api_key: API_KEY,
        language: 'en-US',
      };
      const response = await axios.get(
        `${BASE_URL}/movie/${this.movie_id}/videos?`,
        {
          params,
        }
      );
      return response.data;
    } catch (error) {
      console.log(error);
    }
  }

  resetPage() {
    this.page = 1;
  }
}
